.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 100vw;
}

.Tetris_upper_container {
  display: flex;
  flex-wrap: wrap-reverse;
  flex-direction: row;
  border: #000;
  height: 450px;
  width: 300px;
  position: relative;
}

.above_Tetris_upper_container {
  display: flex;
  flex-wrap: wrap-reverse;
  flex-direction: row;
  height: 450px;
  width: 300px;
  position: absolute;
}

.square {
  box-sizing: border-box;
  font-size: small;
  border: 1px solid #000;
  height: 30px;
  width: 30px;
  position: relative;
}

.square_blue {
  background-color: #439CEF;
}

.square_grey {
  background-color: #303137;
}

.block {
  box-sizing: border-box;
  font-size: small;
  border: 1px solid #000;
  height: 30px;
  width: 30px;
  position: relative;
}

.block_blue {
  background-color: rgba(0, 0, 0, 0.01);
}

.block_grey {
  background-color: #303137;
}

.triangle {
  width: 0;
  height: 0;
  border-top: 7px solid white;
  border-left: 7px solid transparent;
  top: 2px;
  right: 2px;
  position: absolute;
}

.Teris_score_board {
  margin-bottom: 6px;
}
